<template>
  <section class="icons">
    <div class="container">
      <div class="row text-center contact">
        <div
          @click="
            () => {
              gotoLink('facebook');
            }
          "
          class="col-md-3"
        >
          <div class="icon mb-4 bg-gradient">
            <img
              src="https://img.icons8.com/fluent-systems-filled/144/000000/facebook-new.png"
            />
          </div>
          <h3 style="text-align: center">Facebook</h3>
          <!-- <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quas necessitatibus magni.</p> -->
        </div>
        <div
          @click="
            () => {
              gotoLink('email');
            }
          "
          class="col-md-3 contact"
        >
          <div class="icon mb-4">
            <img src="https://img.icons8.com/ios-filled/100/000000/email-open.png" />
          </div>
          <h3 style="text-align: center">Email</h3>
          <!-- <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quas necessitatibus magni.</p> -->
        </div>
        <div
          @click="
            () => {
              gotoLink('telegram');
            }
          "
          class="col-md-3 contact"
        >
          <div class="icon mb-4">
            <img src="https://img.icons8.com/glyph-neue/64/000000/sent.png" />
          </div>
          <h3 style="text-align: center">Telegram</h3>
          <!-- <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quas necessitatibus magni.</p> -->
        </div>
        <div
          @click="
            () => {
              gotoLink('whatsapp');
            }
          "
          class="col-md-3 contact"
        >
          <div class="icon mb-4">
            <img src="https://img.icons8.com/pastel-glyph/128/000000/whatsapp--v2.png" />
          </div>
          <h3 style="text-align: center">Whatsapp</h3>
          <!-- <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quas necessitatibus magni.</p> -->
        </div>
      </div>
    </div>
  </section>
</template>

<style lang="scss" scoped>
.icons {
  .icon {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    font-size: 3rem;
    color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
}
</style>

<script>
import { Capacitor } from "@capacitor/core";
export default {
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
  },

  methods: {
    gotoLink(link) {
      if (link === "whatsapp") {
        window.open(
          "https://api.whatsapp.com/send?phone=+2" + this.adminData.whatsapp,
          "_blank"
        );
      }

      if (link === "email") {
        window.open(
          "mailto:" +
            this.adminData.email +
            "?subject=Subject&body=message%20goes%20here",
          "_blank"
        );
      }

      if (link === "telegram") {
        window.open(this.adminData.telegram);
      }

      if (link === "facebook") {
        if (this.adminData.facebook.includes("messenger"))
          return window.open(this.adminData.facebook, "_blank");
        if (!navigator.platform.includes("Win32") || Capacitor.isNative) {
          window.open("fb://facewebmodal/f?href=" + this.adminData.facebook);
        } else {
          window.open(this.adminData.facebook, "_blank");
        }
      }
    },
  },
};
</script>
